h1.title_text {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 64px;
  line-height: 77px;
  width: 600px;
  color: #000000;
  margin: 0px;
  border: 0px solid #000000;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  justify-self: end;
  letter-spacing: -0.05rem;
}
p.title_description {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 42px;
  width: 600px;
  color: #000000;
  margin: 10px 0px;
}

.title_box {
  position: absolute;
  width: 580px;
  height: 260px;
  top: 300px;
}
.DemoBox {
  position: relative;
  width: 200px;
  height: 229px;
  left: 690px;
  top: 20px;
}

.DemoImg {
  z-index: 0;
}

section.Desktop {
  background-color: var(--section-color);
  display: flex;
  flex-direction: column;
  width: var(--section-width);
  height: var(--section-height);
  background: #ffffff;
  margin: auto;
  position: relative;
}
